import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import api from '../../api/api';

import './Menu.scss';

class Menu extends Component {
  state = {
    menu: [],
    loading: false
  };

  async componentDidMount() {
    this.setState({ loading: true });
      const data = await api.getCategoryList();
      const dataArr = Object.entries(data).map(([key, value], i) => {
        return { id: i, name: key, link: value };
      });
        this.setState({
          menu: [...dataArr],
          loading: false
        });
      }

  render() {
    const { menu, loading } = this.state;
    return (
      <div className="Menu">
        {loading && "Loading..."}
        <h1 className='Menu_title'>Star Wars</h1>
        <ul className="Menu_list">
          <li className="Menu_list-item">
            <Link to='/' className="Menu_list-item-link">Home</Link>
          </li>

          {menu.map(menuItem => (
            <li key={menuItem.id} className="Menu_list-item">
              <Link to={`/${menuItem.name}`} className="Menu_list-item-link">{menuItem.name}</Link>
            </li>
          ))}


        </ul>
      </div>
    );
  }
}
export default Menu;
