import React, { Component } from "react";
import { Switch, Route, Link } from "react-router-dom";
import Searcher from '../Searcher/Searcher';
import Pagination from "../Pagination/Pagination";
import Loader from '../Loader/Loader';
import api from '../../api/api';

class FullRoster extends Component {
  state = {
    roster: [],
    loading: false,
    imageLoadError: true,
    inputValue: '',
    activePage: 1,
    count: null
  };

  async fetchData(page) {
    this.setState({ loading: true });

    const data = await api.getCategoryItems(this.props.match.path);
    this.setState({ 
      roster: data.results,
      loading: false,
      activePage: page,
      count: data.count 
    });
  }

  async componentDidMount() {
    await this.fetchData(this.state.activePage);
  }

  componentDidUpdate(prevProps) {
    if (this.props.match.path !== prevProps.match.path) {
      this.fetchData(this.state.activePage);
    }
  }

  handleButtonClick = page => {
    this.setState({ activePage: page });
  };

  handleSearcheChange = (value) => {
    this.setState({ inputValue: value, loading: true }, () => {
      fetch(`https://swapi.co/api${this.props.match.path}/?search=${this.state.inputValue}`)
      .then(response => response.json())
      .then(data => {
        this.setState({
          roster: data.results,
          loading: false,
          count: data.count
        });
      });
    });
  }

  render() {
    const { roster, loading, activePage, count, inputValue } = this.state;
    const path = this.props.match.path;

    return (
      <div>
        <Searcher inputValue={inputValue} handleSearcheChange={this.handleSearcheChange}/>
        <ul>
          {loading
            ? <Loader />
            : roster.map((item, i) => (
                <li
                  key={i}
                  style={{
                    display: "inline-block",
                    marginRight: 10,
                    border: "1px solid black"
                  }}
                >
                  <Link to={`${path}/${activePage * 10 + i - 9}`}>
                    <img
                      src={`https://starwars-visualguide.com/assets/img${
                        path === "/people" ? "/characters" : path
                      }/${activePage * 10 + i - 9}.jpg`}
                      onError={e =>
                        (e.target.src =
                          "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSCNUQUYAhy8OEM9YVtAHDlUCof3WhX0CzDHnUnynWviRA4Y8D-")
                      }
                      alt={`${path}${i + 1}`}
                    />
                    {item.name ? item.name : item.title}
                  </Link>
                </li>
              ))}
        </ul>

        {loading ? 
        (
          <Loader />
        ) : (
          <Switch>
            <Route
              exact
              path={`${path}`}
              component={() => (
                <Pagination
                  path={path}
                  activePage={activePage}
                  count={count}
                  handleButtonClick={this.handleButtonClick}
                />
              )}
            />
            <Route
              path={`${path}/?page=number`}
              component={() => (
                <Pagination
                  path={path}
                  activePage={activePage}
                  count={count}
                  handleButtonClick={this.handleButtonClick}
                />
              )}
            />
          </Switch>
        )
      }
      </div>
    );
  }
}
export default FullRoster;
