const api = {
  async getCategoryList () {
    const response = await fetch('https://swapi.co/api/');
    const result = await response.json();
    
    return result;
  },

  async getCategoryItems(category) {
    const response = await fetch(`https://swapi.co/api${category}/`);
    const result = await response.json();

    return result;
  },

  async getCategoryItemDetails(category, id) {
    const response = await fetch(`https://swapi.co/api${category}/${id}`);
    const result = await response.json();

    return result;
  },

  async getCategoryPage(category, page) {
    const response = await fetch(`https://swapi.co/api${category}/?page=${page}`);
    const result = await response.json();

    return result;
  }
}
export default api;
