import React, { Component } from "react";
import { Link } from "react-router-dom";

class Pagination extends Component {
  render() {
    const { activePage, count, handleButtonClick } = this.props;
    const path = this.props.path;
    const pagesCount = Math.ceil(count / 10);
    const pages = Array(pagesCount)
      .fill(0)
      .map((_, i) => i + 1);

    return (
      <div>
        {pages.map(p => (
          <Link to={`${path}/?page=${activePage}`} key={p}>
            <button
              style={p === activePage ? { background: "red" } : {}}
              onClick={() => handleButtonClick(p)}
            >
              {p}
            </button>
          </Link>
        ))}
      </div>
    );
  }
}

export default Pagination;
